import React, { useEffect, useState } from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  useParams
} from "react-router-dom";
import Node from './node';

export default function App() {
  return (
    <Router>
        <Switch>
            <Route path="/:id" children={<Child />} />
        </Switch>
    </Router>
  );
}

function getInnerTitle(sect) {
    if (sect["title"]) {
        return sect["title"]["title"];
    } else {
        return null;
    }
} 

function dbDriver(func) { // Array, ["title"]
    var f = function(db,dels){
        if (dels.length === 0) {
            return
        }

        let sect = db.findIndex(sect => ((sect["topic"] || getInnerTitle(sect) || sect["title"] || sect) === dels[0]))
        if (sect >= 0) {
            if (dels.length === 1) {
                func(db,sect)
            } else {
                f(db[sect]["list"] || db[sect]["desc"],dels.slice(1))
            }
        }
    }
    return f;
}

let delSubTitle = dbDriver(function(db,i){ db.splice(i,1) })
/* let addItem = function(item) { return dbDriver(function(db, i) {
    let size = db[i]["list"].length
    if (db[i]["list"][size-1]["title"] !== item["title"]) {
        db[i]["list"].push(item)
    }
}) } */

// del :: ["topic", "title", "title"...]
function genDB(dels) {
    let db = require('./db.json')
    dels.forEach(del => {
        delSubTitle(db, del)
    })
    return db
}

function Child() {
    const [dels, setDels] = useState(null);
    const [ver, setVer] = useState("null");
    let { id } = useParams();
    useEffect(() => {
        if (ver !== id) {
            setVer(id);
            let newDels = [
                ["Reading", "Functional programming"],
                ["Reading", "Verification Automation"],
                ["Reading", "Programming Language Theory"],
                ["Experience","Freelance Programmer"]
            ];
            if (id.length === 0 || id[0] !== "r") {
                newDels = null;
            } else {
                if (id === "rsys") {
                    newDels = newDels.concat([
                        ["Linux Administration"],
                        ["Cloud service"],
                        ["Reading", "Rails"],
                        ["Side project", "OnePageResume"]
                    ]);
                } else if (id === "rweb") {
                    newDels = newDels.concat([
                        //["Dev Tools", "systemtap"],
                        ["Dev Tools", "qemu"],
                        ["Dev Tools", "Trex traffic generator"],
                        ["Dev Tools", "buildroot"],
                        ["Side project", "craft-os"],
                        ["Side project", "xv6-labs"],
                    ]);
                } else if (id === "rfull") {
                    newDels = []
                } else {
                    newDels = null
                }
            }
            setDels(newDels)
        }
      }, [dels, id, ver])

    if (dels !== null) {
        return (
            <div id="page-wrap">
                <div id="contact-info" className="vcard">
                
                    <h1 className="fn">Chung-Feng Zheng</h1>
                
                    <p>
                        Cell: <span className="tel">0955-088-153</span><br />
                        Email: <a className="email" href="mailto:zhengcf@cs.nctu.edu.tw">zhengcf@cs.nctu.edu.tw</a>
                    </p>
                </div>
                <dl>
                    {genDB(dels).map(n => <Node now={n} />)}
                </dl>
            </div>
        );
    } else {
        return (null)
    }
}
